/* entire container, keeps perspective */
.flip-container {
  perspective: 1000;
  transform-style: preserve-3d;
  color: #fff;
  display: inline-block;
  margin: 16px;
  vertical-align: top; }

.flip-container, .front, .back {
  width: 165px;
  height: 165px;
  border-radius: 82px;
  text-align: center; }

/* flip speed goes here */
.card {
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
  display: block;
  float: left;
  width: 100%;
  cursor: pointer; }

.back-inner {
  max-width: 85%;
  display: block;
  margin: 0 auto;
  line-height: 1.2;
  font-size: 14px; }

/* hide back of pane during swap */
.front, .back {
  backface-visibility: hidden;
  transition: 0.6s;
  transform-style: preserve-3d;
  position: absolute;
  top: 0;
  left: 0;
  border: solid 1px #5e6367;
  margin: 0; }

/*  UPDATED! front pane, placed above back */
.front {
  z-index: 2;
  transform: rotateY(0deg);
  background: transparent;
  font-size: 48px;
  color: #5e6367; }

/* back, initially hidden pane */
.back {
